import DatetimePicker from '@/DatetimePicker/index.vue'
import {Toast} from 'vant';
import 'vant/lib/toast/style';
export default{
  data(){
    return {
      startTime:'',
      endTime:'',
      birthDay:'',
    }
  },
  components:{
    DatetimePicker,
  },
  methods:{
    otherCallFn(val){
      console.log('otherCallFn==',val)
    },
    getDate(data){
      console.log('获取到时间000==',data)
      let {target,time}=data
      let timeStr=new Date(time.replace(/-/g,'/')).getTime();
      console.log(target==0,target==1)
      //Toast.setDefaultOptions({ duration: 200000000 });
      if(target==0){
        if(this.endTime && ((timeStr - new Date(this.endTime).getTime())>0)){
          return this.$Toast('截止日期不能小于开始日期')
        }
        this.startTime=time
        this.otherCallFn(target,time)
      }else if(target==1){
        if(this.startTime && ((new Date(this.startTime).getTime() - timeStr)>0)){
          return this.$Toast('截止日期不能小于开始日期')
        }
        this.endTime=time
        this.otherCallFn(target,time)
      }else if(target==2){//出生日期
      if((this.endTime && ((timeStr - new Date(this.endTime).getTime())>0)) ||
        this.startTime && ((timeStr - new Date(this.startTime).getTime())>0)
      ){
        return this.$Toast('出生日期不能小于开始日期或截止日期')
      }
        this.birthDay=time
      }
    },
    dateDialogShow(val){
      this.$refs.date.openShow(val)
    },
    toast(message,type='html'){//报错toast
      Toast({
        type,
        message,
        forbidClick: true,
      });
    },
  }
}