<template>
  <div class="DatetimePicker" v-if="dataShow">
    <div class="main phone-pad">
      <div class='flex DatetimePicker-bar font-bold space_between'>
        <div class="cancel" @click.stop='dateCancel'>取消</div>
        <div>{{target==0 ? '选择起始日期' : target==1 ? '选择截止日期':''}}</div>
        <div @click='confirm' class="comfirm">确定</div>
      </div>
      <div class="con">
        <van-datetime-picker
          v-model="currentDate"
          :show-toolbar=false 
          type="date"
          :min-date="minDate"
          :max-date="maxDate"
          item-height='50' 
          @change="dataChange"
          :swipe-duration='100'
          visible-item-count=5 
          :formatter="formatter"
        />
      </div>
    </div>
  </div>
</template>

<script>
  import DatetimePicker from 'vant/lib/datetime-picker';
  import 'vant/lib/datetime-picker/style';
  

  // const minDate=new Date(1950, 1, 1);
  const minDate=new Date((new Date()).getFullYear()-90, 0, 1);
  console.log('多少来着===',(new Date()).getFullYear()+40)
  export default{
    name:'DatetimePicker',
    components:{
      [DatetimePicker.name]:DatetimePicker,
    },
    data(){
      return {
        dataShow:false,
        currentDate:new Date(),
        minDate,
        maxDate:new Date((new Date()).getFullYear()+40, 12, 31),
        target:0,
        saveMinDate:minDate,
      }
    },
    props:{
      startTime:{
        type:String,
        default(){
          return ''
        }
      },
    },
    created(){
      this.saveMinDate=minDate;
    },
    activated(){
      this.saveMinDate=minDate;
    },
    watch:{
      startTime:function(val){
        // if(val){
        //   console.log('监听到的数据startTime=',val)
        //   let val=val.replace(/^-$/,'/');
        //   this.minDate=new Date(val)
        //   console.log('this.minDate==',this.minDate)
        // }
      },
    },
    methods:{
      formatter(type, val) {
        if (type === 'year') {
          return val + '年';
        }
        if (type === 'month') {
          return val + '月';
        }
        if (type === 'day') {
          return val + '日';
        }
        return val;
      },
      openShow(val){
        console.log('val=',val)
        this.dataShow=true;
        this.target=val
        if(this.target==0){
          this.minDate=minDate;
        }else if(this.target==1){
          this.minDate=this.saveMinDate
        }else if(this.target==2){
          this.minDate=minDate;
        }
        setTimeout(()=>{
          this.setItemCol()
        },100)
      },
      dateCancel(){
        this.dataShow=false;
      },
      setItemCol(){
        this.$nextTick(()=>{
          $('li.van-picker-column__item--selected').prev().css({'color':'rgba(100,100,105,0.60)'});
        })
      },
      dataChange(){
        //console.log('change=e',{e,currentDate:this.currentDate})
        this.setItemCol()
      },
      getCurrentTime(_date,val='-'){
        let mon=_date.getMonth()+1
        if(mon<10){
          mon='0'+mon;
        }
        let d=_date.getDate()
        if(d<10){
          d='0'+d;
        }
        let str=_date.getFullYear()+val+mon+val+d
        return str
      },
      confirm(){
        let _date = new Date(this.currentDate)
        let checkTime=_date.getTime()
        let oneDay=60*60*24*1000;
        // let mon=_date.getMonth()+1
        // if(mon<10){
        //   mon='0'+mon;
        // }
        // let d=_date.getDate()
        // if(d<10){
        //   d='0'+d;
        // }
        // let str=_date.getFullYear()+'-'+mon+'-'+d
        //console.log('确定==',this.currentDate,str)
        //minDate,maxDate,
        let str=this.getCurrentTime(_date)
        let target=this.target
        //let otherStr=this.getCurrentTime(new Date(checkTime-oneDay))
        let otherStr='';
        if(target==0){
          otherStr=this.getCurrentTime(new Date(checkTime+oneDay),'/')
          this.minDate=new Date(otherStr);
          this.saveMinDate=this.minDate;
          //this.currentDate=new Date(otherStr);
        }else if(target==1){
          //otherStr=this.getCurrentTime(new Date(checkTime-oneDay),'/')
          //this.currentDate=new Date(otherStr);
        }else{
          //this.currentDate=new Date();
          this.minDate=new Date();
        }
        console.log('当前时间戳--',checkTime,otherStr)
        this.$emit('getDate',{time:str,target})
        this.dateCancel()
      },
    }
  }
</script>

